<template>
  <v-card flat class="search-card h-100">
    <v-card-title class="search-card-title py-1 px-4">
      <v-icon small color="primary" class="mr-2">mdi-magnify</v-icon>
      <span class="text-subtitle-2">Advanced Search</span>
    </v-card-title>
    <v-card-text class="pa-2">
      <v-row dense>
        <v-col cols="12" sm="6" md="3" class="py-1">
          <v-text-field
            v-model="localSearch.keyword"
            label="Search by keyword"
            placeholder="Case ID, Vehicle No, etc"
            prepend-inner-icon="mdi-text-search"
            clearable
            outlined
            dense
            hide-details
            @input="emitUpdate"
          ></v-text-field>
        </v-col>
        <v-col cols="12" sm="6" md="3" class="py-1">
          <v-select
            v-model="localSearch.dept"
            :items="departments"
            item-text="name"
            item-value="id"
            label="Department"
            outlined
            dense
            clearable
            hide-details
            prepend-inner-icon="mdi-domain"
            @change="emitUpdate"
          ></v-select>
        </v-col>
        <v-col cols="12" sm="6" md="3" class="py-1">
          <v-select
            v-model="localSearch.branch"
            :items="branches"
            item-text="name"
            item-value="id"
            label="Branch"
            outlined
            dense
            clearable
            hide-details
            prepend-inner-icon="mdi-office-building"
            @change="emitUpdate"
          ></v-select>
        </v-col>
        <v-col cols="12" sm="6" md="3" class="py-1">
          <v-autocomplete
            v-model="localSearch.insurer"
            :items="insurers"
            item-text="name"
            item-value="id"
            label="Insurer"
            outlined
            dense
            clearable
            hide-details
            prepend-inner-icon="mdi-shield-account"
            @change="emitUpdate"
          ></v-autocomplete>
        </v-col>
      </v-row>
      <v-row dense>
        <v-col cols="12" sm="6" md="3" class="py-1">
          <v-select
            v-model="localSearch.status"
            :items="stages"
            item-text="name"
            item-value="stageCode"
            label="Status"
            outlined
            dense
            clearable
            hide-details
            prepend-inner-icon="mdi-flag"
            @change="emitUpdate"
          ></v-select>
        </v-col>
        <v-col cols="12" sm="6" md="9" class="d-flex justify-end pt-2 pb-0">
          <v-btn
            color="primary"
            class="mr-2"
            @click="emitSearch"
            :loading="searchLoading"
            :disabled="!isValid"
            small
          >
            <v-icon left small>mdi-magnify</v-icon>
            Search
          </v-btn>
          <v-btn text small @click="emitReset">Clear</v-btn>
        </v-col>
      </v-row>
    </v-card-text>
  </v-card>
</template>

<script>
export default {
  name: "AdvancedSearch",
  props: {
    departments: { type: Array, default: () => [] },
    branches: { type: Array, default: () => [] },
    insurers: { type: Array, default: () => [] },
    stages: { type: Array, default: () => [] },
    searchLoading: { type: Boolean, default: false },
    advancedSearch: { type: Object, default: () => ({}) },
    isValid: { type: Boolean, default: false }, // Ensure Boolean type
  },
  data() {
    return {
      localSearch: { ...this.advancedSearch },
    };
  },
  watch: {
    advancedSearch: {
      handler(val) {
        this.localSearch = { ...val };
      },
      deep: true,
    },
  },
  methods: {
    emitSearch() {
      this.$emit("search");
    },
    emitReset() {
      this.$emit("reset");
    },
    emitUpdate() {
      this.$emit("update", { ...this.localSearch });
    },
  },
};
</script>

<style scoped>
.search-card {
  border: 1px solid rgba(0, 0, 0, 0.05);
  border-radius: 4px;
}
.search-card-title {
  border-bottom: 1px solid rgba(0, 0, 0, 0.05);
  background-color: rgba(0, 0, 0, 0.01);
}
</style>
