}

/* Responsive styles for pagination bar */
@media (max-width: 600px) {
  .pagination-bar {
    flex-direction: column !important;
    align-items: stretch !important;
    gap: 4px;
  }
  .pagination-controls {
    flex-wrap: wrap;
    gap: 2px;
    margin-bottom: 4px;
  }
  .pagination-btn {
    min-width: 28px;
    height: 28px;
    font-size: 13px;
    padding: 0 2px;
  }
  .pagination-select {
    min-width: 60px;
    max-width: 70px;
    font-size: 13px;
  }
  .pagination-jump {
    min-width: 40px;
    max-width: 50px;
    font-size: 13px;
  }
  .pagination-total {
    font-size: 13px;
    margin-left: 0 !important;
  }
  .pagination-ellipsis {
    font-size: 16px;
  }
}

/* Add Case Button Bar styles */
.add-case-bar {
  width: 100%;
  justify-content: flex-start;
  margin-bottom: 8px;
}

.add-case-btn {
  min-width: 140px;
  height: 44px;
  font-size: 18px;
  font-weight: 500;
  text-transform: none;
  letter-spacing: 0.5px;
  border-radius: 22px;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.08);
  display: flex;
  align-items: center;
  justify-content: center;
}

/* Responsive: keep button full width on mobile */
@media (max-width: 600px) {
  .add-case-bar {
    justify-content: stretch;
  }
  .add-case-btn {
    width: 100%;
    min-width: 0;
    font-size: 16px;
    height: 40px;
    border-radius: 20px;
  }
}

/* Remove FAB styles if present */
.add-case-fab {
  display: none !important;
}
